export default {
name: "mutli-practice",
  props:[
    'options',
    'correct',
    'value'
  ],
  emits: ['input'],
  data:()=>({
    v:''
  }),
  watch:{
  v: function(val){
    console.log(val);
    if (val==this.correct){
      this.$emit('input',true)
    }
    else {
      this.$emit('input',false)
    }
  }
  }

}
